@import url('https://fonts.googleapis.com/css2?family=Mohave:wght@300;400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');

.app {
  font-family: 'Poppins', sans-serif;
  line-height: 1.5;
  color: var(--clr-fg);
  background: var(--clr-bg-img);
  /* background-color: var(--clr-bg); */
}

.light {
  --clr-bg: #dbd6fb;
  --clr-bg-alt: #e4e0fb;
  --clr-bg-img:  linear-gradient(to right top, #dae0ee, #d8dbf6, #ddd5fb, #e7cdfd, #f6c4f9);
  --clr-fg: #3c3b3c;
  --clr-fg-alt: #6f5e92;
  --clr-primary: #2a2438;
  --shadow: rgba(100, 100, 111, 0.2) 0px 7px 29px 0px;
}

.dark {
  --clr-bg: #162d36;
  --clr-bg-alt: #36555e;
  --clr-bg-img: linear-gradient(62deg, #2C5364, #203A43, #0F2027);
  --clr-fg: #dbf7ff;
  --clr-fg-alt: #bcecf8;
  --clr-primary: #00adb5;
  --shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px,
    rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;
}

main {
  max-width: 1100px;
  width: 95%;
  margin: 0 auto;
}

.section {
  margin-top: 5em;
}

.section__title {
  text-align: center;
  margin-bottom: 0.6em;
  text-transform: uppercase;
}

.center {
  display: flex;
  align-items: center;
}

.link {
  color: var(--clr-primary);
  padding: 0 0 0.3em 0;
  position: relative;
}

.link:hover {
  color: var(--clr-primary);
}

.link::before {
  border-radius: 15px;
  content: '';
  display: inline;
  width: 0%;
  height: 0.2em;
  position: absolute;
  bottom: 0;
  background-color: var(--clr-primary);
  transition: width 0.2s ease-in;
}

.link:hover::before,
.link:focus::before {
  width: 100%;
}

.link--nav {
  color: var(--clr-fg);
  font-weight: 500;
}

.link--icon {
  color: var(--clr-fg);
}

.btn {
  display: block;
  cursor: pointer;
  padding: 0.8em 1.4em;
  font-weight: 500;
  font-size: 0.9rem;
  transition: transform 0.2s ease-in-out;
}

.btn--outline {
  color: var(--clr-primary);
  border: 2px solid var(--clr-primary);
  position: relative;
  overflow: hidden;
  z-index: 1;
}

.btn--outline:hover,
.btn--outline:focus {
  color: var(--clr-bg);
}

.btn--outline:before {
  content: '';
  position: absolute;
  background-color: var(--clr-primary);
  right: 100%;
  bottom: 0;
  left: 0;
  top: 0;
  z-index: -1;
  transition: right 0.2s ease-in-out;
}

.btn--outline:hover:before,
.btn--outline:focus:before {
  right: 0;
}

.btn--plain {
  border-radius: 25px;
  text-transform: initial;
  background-color: var(--clr-bg-alt);
  box-shadow: rgba(0, 0, 0, 0.15) 0px 3px 3px 0px;
  border: 0;
}

.btn--plain:hover {
  transform: translateY(-4px);
}

.btn--icon {
  padding: 0;
}

.btn--icon:hover,
.btn--icon:focus {
  color: var(--clr-primary);
}

.btn--icon:active {
  transform: translateY(-5px);
}

.resume-btn {
  width: 7.2em;
  margin-top: 1rem;
  border-radius: 15px;
}

.cont-btn {
  display: flex;
  margin-left: 15px;
}

.SandT {
  display: flex;
  justify-content: center;
}

.section__title:after {
  border-bottom: 0.2em solid var(--clr-primary);
  content: '';
  display: block;
  margin: auto;
  width: 1.5em;
  border-radius: 1.5em;
}

.noSelect {
  -webkit-tap-highlight-color: transparent;
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.noSelect:focus {
  outline: none !important;
}

::-webkit-scrollbar {
  width: 0.4em;
}

::-webkit-scrollbar-track {
  background: #3e4655;
}

::-webkit-scrollbar-thumb {
  background: #e9e9e9;
  border-radius: 12px;
}

@media (max-width: 600px) {
  .section {
    margin-top: 4em;
  }
  .SandT {
    display: block;
  }
}
